import React from "react"
import { checkPropTypes } from "prop-types";
import {copyrightStyle, facebookStyle, footerStyle, instagramStyle, socialStyle, twitterStyle} from './styles/footer.styles';
import { graphql, useStaticQuery } from "gatsby";
import Img, { FixedObject } from "gatsby-image"

export interface FooterProps{

}

export default function Footer (props : FooterProps) {

    const data = useStaticQuery(
        graphql`
            query {
                facebook: file(relativePath: { eq: "facebook.png" }) {
                    childImageSharp {
                    fixed(width: 15, height: 22) {
                        ...GatsbyImageSharpFixed
                    }
                    }
                }
                twitter: file(relativePath: { eq: "twitter.png" }) {
                    childImageSharp {
                    fixed(width: 19, height: 17) {
                        ...GatsbyImageSharpFixed
                    }
                    }
                }
                instagram: file(relativePath: { eq: "instagram.png" }) {
                    childImageSharp {
                    fixed(width: 22, height: 22) {
                        ...GatsbyImageSharpFixed
                    }
                    }
                }
            }
        `)

  return(
    <footer style={footerStyle}>
        <div style={socialStyle}>
            {/*<Img fixed={data.facebook.childImageSharp.fixed} style={facebookStyle}/>
            <Img fixed={data.twitter.childImageSharp.fixed} style={twitterStyle}/>
            <Img fixed={data.instagram.childImageSharp.fixed} style={instagramStyle}/>*/}
        </div>
        <div style={copyrightStyle}>
            Copyright 2020. All rights reserved.
        </div>
    </footer>
  );
}